export default {
	data() {
		return {
			userlList: [],
		}
	},
	onLoad: function() {
		uni.request({
			url: 'http://'+getApp().globalData.urlStr+'/GetUsersByGroupId',
			method: 'GET',
			data: {
				groupId: getApp().globalData.groupId,
			},
			success: res => {
				if (res.data.code == 0) {
					this.userlList = res.data.data;
				}
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		selectUser(e) {
			getApp().globalData.uId = e
			uni.navigateTo({
				url: "../user/userDetail"
			})
		}
	}
}
